@import url(https://fonts.googleapis.com/css?family=Raleway:400,700);
.wrap {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.3rem;
}

.button {
    width: 140px;
    height: 45px;
    font-family: 'Roboto', sans-serif;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #000;
    background-color: #fff;

    border: none;
    border-radius: 45px;
    box-shadow: 0px 8px 15px rgba(20, 20, 25, 0.15);
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
}

.button:hover {
    background-color: #2EE59D;
    box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
    color: #fff;
    transform: translateY(-7px);
}

#DropBar {
    text-decoration: none;
    color: #000;
    font-size: 1.2rem;
    font-weight: 400;
    cursor: pointer;
}




.snip1445 {
    font-family: 'Raleway', Arial, sans-serif;
    position: relative;
    overflow: hidden;
    min-width: 230px;
    max-width: 315px;
    width: 100%;
    color: #ffffff;
    text-align: center;
    font-size: 16px;
    background-color: #000000;

}
.snip1445 *,
.snip1445 *:before,
.snip1445 *:after {
    box-sizing: border-box;
    transition: all 0.55s ease;
}
.snip1445 img {
    max-width: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    vertical-align: top;
}
.snip1445 figcaption {
    position: absolute;
    bottom: 25%;
    right: 25%;
    padding: 5px 10px 10px;
}
.snip1445 figcaption:before,
.snip1445 figcaption:after {
    height: 2px;
    width: 400px;
    position: absolute;
    content: '';
    background-color: #ffffff;
}
.snip1445 figcaption:before {
    top: 0;
    left: 0;
    transform: translateX(100%);
}
.snip1445 figcaption:after {
    bottom: 0;
    right: 0;
    transform: translateX(-100%);
}
.snip1445 figcaption div:before,
.snip1445 figcaption div:after {
    width: 2px;
    height: 300px;
    position: absolute;
    content: '';
    background-color: #ffffff;
}
.snip1445 figcaption div:before {
    top: 0;
    left: 0;
    transform: translateY(100%);
}
.snip1445 figcaption div:after {
    bottom: 0;
    right: 0;
    transform: translateY(-100%);
}
.snip1445 h2,
.snip1445 h4 {
    margin: 0;
    text-transform: uppercase;
}
.snip1445 h2 {
    font-weight: 400;
}
.snip1445 h4 {
    display: block;
    font-weight: 700;
    background-color: #ffffff;
    padding: 5px 10px;
    color: #000000;
}
.snip1445 a {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
.snip1445:hover img,
.snip1445.hover img {
    zoom: 1;
    filter: alpha(opacity=50);
    -webkit-opacity: 0.5;
    opacity: 0.5;
}
.snip1445:hover figcaption:before,
.snip1445.hover figcaption:before,
.snip1445:hover figcaption:after,
.snip1445.hover figcaption:after,
.snip1445:hover figcaption div:before,
.snip1445.hover figcaption div:before,
.snip1445:hover figcaption div:after,
.snip1445.hover figcaption div:after {
    transform: translate(0, 0);
}
.snip1445:hover figcaption:before,
.snip1445.hover figcaption:before,
.snip1445:hover figcaption:after,
.snip1445.hover figcaption:after {
    transition-delay: 0.15s;
}

